"use client";

import { useState } from "react";
import ExcelGrid from "@/components/ExcelGrid";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Progress } from "@/components/ui/progress";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";

// Simplified inventory data for employer access (read-only with limited write access)
const mockEmployerInventoryData = [
  {
    id: 1,
    sku: "SKU001",
    name: "iPhone 15 Pro Max",
    category: "Electronics",
    currentStock: 25,
    minStock: 10,
    maxStock: 100,
    status: "In Stock",
    location: "A1-B2-C3",
    lastUpdated: "2024-01-15",
    canEdit: true // Employer can edit stock levels
  },
  {
    id: 2,
    sku: "SKU002",
    name: "Samsung Galaxy S24",
    category: "Electronics",
    currentStock: 5,
    minStock: 10,
    maxStock: 50,
    status: "Low Stock",
    location: "A1-B3-C1",
    lastUpdated: "2024-01-14",
    canEdit: true
  },
  {
    id: 3,
    sku: "SKU003",
    name: "MacBook Air M3",
    category: "Computers",
    currentStock: 0,
    minStock: 5,
    maxStock: 25,
    status: "Out of Stock",
    location: "B1-A2-C4",
    lastUpdated: "2024-01-13",
    canEdit: false // Cannot edit out of stock items
  },
  {
    id: 4,
    sku: "SKU004",
    name: "Dell XPS 13",
    category: "Computers",
    currentStock: 15,
    minStock: 8,
    maxStock: 30,
    status: "In Stock",
    location: "B1-A3-C2",
    lastUpdated: "2024-01-15",
    canEdit: true
  },
  {
    id: 5,
    sku: "SKU005",
    name: "AirPods Pro 2",
    category: "Accessories",
    currentStock: 45,
    minStock: 20,
    maxStock: 100,
    status: "In Stock",
    location: "C1-A1-B2",
    lastUpdated: "2024-01-15",
    canEdit: true
  }
];

// Simplified columns for employer access
const employerInventoryColumns = [
  { key: 'sku', label: 'SKU', type: 'text' as const, width: 'w-32' },
  { key: 'name', label: 'Product Name', type: 'text' as const, width: 'w-48' },
  { key: 'category', label: 'Category', type: 'text' as const, width: 'w-32' },
  { key: 'currentStock', label: 'Current Stock', type: 'number' as const, editable: true, width: 'w-32' },
  { key: 'minStock', label: 'Min Stock', type: 'number' as const, width: 'w-28' },
  { key: 'status', label: 'Status', type: 'badge' as const, width: 'w-32' },
  { key: 'location', label: 'Location', type: 'text' as const, width: 'w-32' },
  { key: 'lastUpdated', label: 'Last Updated', type: 'date' as const, width: 'w-32' }
];

const recentActivities = [
  {
    id: 1,
    action: "Stock Updated",
    product: "iPhone 15 Pro Max",
    details: "Stock increased from 20 to 25",
    timestamp: "2024-01-15 14:30",
    user: "Employer"
  },
  {
    id: 2,
    action: "Low Stock Alert",
    product: "Samsung Galaxy S24",
    details: "Stock below minimum threshold",
    timestamp: "2024-01-15 12:15",
    user: "System"
  },
  {
    id: 3,
    action: "Stock Updated",
    product: "AirPods Pro 2",
    details: "Stock decreased from 50 to 45",
    timestamp: "2024-01-15 10:45",
    user: "Employer"
  }
];

export default function EmployerPage() {
  const [inventoryData, setInventoryData] = useState(mockEmployerInventoryData);
  const [selectedProducts, setSelectedProducts] = useState<any[]>([]);
  const [syncStatus, setSyncStatus] = useState("Connected");

  // Calculate stats
  const totalProducts = inventoryData.length;
  const inStock = inventoryData.filter(item => item.status === 'In Stock').length;
  const lowStock = inventoryData.filter(item => item.status === 'Low Stock').length;
  const outOfStock = inventoryData.filter(item => item.status === 'Out of Stock').length;
  const editableProducts = inventoryData.filter(item => item.canEdit).length;

  const handleDataChange = (newData: any[]) => {
    // Update status based on stock levels and sync with main admin file
    const updatedData = newData.map(item => ({
      ...item,
      status: item.currentStock === 0 ? 'Out of Stock' : 
              item.currentStock <= item.minStock ? 'Low Stock' : 'In Stock',
      lastUpdated: new Date().toISOString().split('T')[0],
      canEdit: item.currentStock > 0 // Can only edit if not out of stock
    }));
    
    setInventoryData(updatedData);
    
    // Simulate sync with admin file
    setSyncStatus("Syncing...");
    setTimeout(() => {
      setSyncStatus("Synced");
      setTimeout(() => setSyncStatus("Connected"), 2000);
    }, 1000);
  };

  const handleRowSelect = (selected: any[]) => {
    setSelectedProducts(selected);
  };

  const requestStockUpdate = () => {
    if (selectedProducts.length === 0) {
      alert("Please select products to request stock updates");
      return;
    }
    
    alert(`Stock update request sent to admin for ${selectedProducts.length} products`);
  };

  const reportIssue = () => {
    alert("Issue reported to admin. You will receive a response shortly.");
  };

  const syncWithAdmin = () => {
    setSyncStatus("Syncing...");
    setTimeout(() => {
      setSyncStatus("Synced");
      alert("Data synchronized with admin successfully");
      setTimeout(() => setSyncStatus("Connected"), 2000);
    }, 1500);
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Employer Inventory Access</h1>
          <p className="text-gray-600">Limited inventory operations with real-time sync to admin</p>
        </div>
        
        <div className="flex items-center gap-2">
          <Badge variant={syncStatus === "Connected" ? "default" : syncStatus === "Syncing..." ? "secondary" : "outline"}>
            {syncStatus}
          </Badge>
          <Button onClick={syncWithAdmin} variant="outline" size="sm">
            Sync Now
          </Button>
          <Button onClick={reportIssue} variant="outline" size="sm">
            Report Issue
          </Button>
        </div>
      </div>

      {/* Access Level Alert */}
      <Alert className="border-blue-200 bg-blue-50">
        <AlertDescription className="text-blue-800">
          <strong>Employer Access Level:</strong> You can update stock quantities for available products. 
          All changes sync automatically with the main admin file. Contact admin for full access.
        </AlertDescription>
      </Alert>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Total Products</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{totalProducts}</div>
            <p className="text-xs text-gray-600">{editableProducts} editable</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">In Stock</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">{inStock}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Low Stock</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-orange-600">{lowStock}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Out of Stock</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-red-600">{outOfStock}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Sync Status</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-lg font-bold text-blue-600">{syncStatus}</div>
          </CardContent>
        </Card>
      </div>

      {/* Alerts */}
      {lowStock > 0 && (
        <Alert className="border-orange-200 bg-orange-50">
          <AlertDescription className="text-orange-800">
            <strong>{lowStock} products</strong> are running low on stock. Consider requesting restock from admin.
          </AlertDescription>
        </Alert>
      )}

      {outOfStock > 0 && (
        <Alert className="border-red-200 bg-red-50">
          <AlertDescription className="text-red-800">
            <strong>{outOfStock} products</strong> are out of stock. Contact admin for immediate restocking.
          </AlertDescription>
        </Alert>
      )}

      <Tabs defaultValue="inventory" className="space-y-6">
        <TabsList>
          <TabsTrigger value="inventory">Inventory Management</TabsTrigger>
          <TabsTrigger value="activity">Recent Activity</TabsTrigger>
          <TabsTrigger value="requests">Stock Requests</TabsTrigger>
        </TabsList>

        <TabsContent value="inventory" className="space-y-6">
          {/* Inventory Grid */}
          <Card>
            <CardHeader>
              <CardTitle>Inventory Operations</CardTitle>
              <CardDescription>
                Update stock quantities for available products. Changes sync automatically with admin.
              </CardDescription>
            </CardHeader>
            <CardContent className="p-6">
              <ExcelGrid
                data={inventoryData}
                columns={employerInventoryColumns}
                onDataChange={handleDataChange}
                onRowSelect={handleRowSelect}
                title="Product Inventory (Employer Access)"
                searchable={true}
                filterable={true}
                exportable={false} // Limited export access
                addNewRow={false} // Cannot add new products
              />
            </CardContent>
          </Card>

          {/* Quick Actions */}
          <Card>
            <CardHeader>
              <CardTitle>Quick Actions</CardTitle>
              <CardDescription>Available operations for employer access</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                <Button variant="outline" className="h-20 flex-col" onClick={requestStockUpdate}>
                  <span className="font-semibold">Request Stock Update</span>
                  <span className="text-xs text-gray-600">Send to Admin</span>
                </Button>
                <Button variant="outline" className="h-20 flex-col" onClick={syncWithAdmin}>
                  <span className="font-semibold">Sync Data</span>
                  <span className="text-xs text-gray-600">With Admin File</span>
                </Button>
                <Button variant="outline" className="h-20 flex-col" onClick={reportIssue}>
                  <span className="font-semibold">Report Issue</span>
                  <span className="text-xs text-gray-600">Contact Admin</span>
                </Button>
                <Button variant="outline" className="h-20 flex-col" disabled>
                  <span className="font-semibold">Add Product</span>
                  <span className="text-xs text-gray-600">Admin Only</span>
                </Button>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="activity" className="space-y-6">
          {/* Recent Activity */}
          <Card>
            <CardHeader>
              <CardTitle>Recent Activity</CardTitle>
              <CardDescription>Your recent inventory operations and system updates</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {recentActivities.map(activity => (
                  <div key={activity.id} className="flex items-start justify-between p-4 border rounded-lg">
                    <div className="flex-1">
                      <div className="flex items-center gap-2 mb-1">
                        <Badge variant={activity.user === "Employer" ? "default" : "secondary"}>
                          {activity.action}
                        </Badge>
                        <span className="text-sm text-gray-600">by {activity.user}</span>
                      </div>
                      <h4 className="font-semibold">{activity.product}</h4>
                      <p className="text-sm text-gray-600">{activity.details}</p>
                    </div>
                    <div className="text-xs text-gray-500 text-right">
                      {activity.timestamp}
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Activity Summary */}
          <Card>
            <CardHeader>
              <CardTitle>Activity Summary</CardTitle>
              <CardDescription>Your contribution to inventory management</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div className="text-center p-4 border rounded">
                  <div className="text-2xl font-bold text-blue-600">12</div>
                  <div className="text-sm text-gray-600">Stock Updates Today</div>
                </div>
                <div className="text-center p-4 border rounded">
                  <div className="text-2xl font-bold text-green-600">45</div>
                  <div className="text-sm text-gray-600">Updates This Week</div>
                </div>
                <div className="text-center p-4 border rounded">
                  <div className="text-2xl font-bold text-purple-600">98%</div>
                  <div className="text-sm text-gray-600">Sync Success Rate</div>
                </div>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="requests" className="space-y-6">
          {/* Stock Requests */}
          <Card>
            <CardHeader>
              <CardTitle>Stock Requests</CardTitle>
              <CardDescription>Request stock updates or new products from admin</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                <Alert className="border-blue-200 bg-blue-50">
                  <AlertDescription className="text-blue-800">
                    Use this section to request stock updates for products you cannot edit directly, 
                    or to request new products to be added to the inventory.
                  </AlertDescription>
                </Alert>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <Button variant="outline" className="h-24 flex-col" onClick={requestStockUpdate}>
                    <span className="font-semibold">Request Stock Reorder</span>
                    <span className="text-xs text-gray-600">For low/out of stock items</span>
                  </Button>
                  <Button variant="outline" className="h-24 flex-col">
                    <span className="font-semibold">Request New Product</span>
                    <span className="text-xs text-gray-600">Add to inventory</span>
                  </Button>
                  <Button variant="outline" className="h-24 flex-col">
                    <span className="font-semibold">Request Price Update</span>
                    <span className="text-xs text-gray-600">Pricing changes</span>
                  </Button>
                  <Button variant="outline" className="h-24 flex-col">
                    <span className="font-semibold">Request Access</span>
                    <span className="text-xs text-gray-600">Additional permissions</span>
                  </Button>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Pending Requests */}
          <Card>
            <CardHeader>
              <CardTitle>Pending Requests</CardTitle>
              <CardDescription>Your submitted requests awaiting admin approval</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-3">
                <div className="flex items-center justify-between p-3 border rounded">
                  <div>
                    <span className="font-medium">Stock Reorder: MacBook Air M3</span>
                    <p className="text-sm text-gray-600">Requested 10 units</p>
                  </div>
                  <Badge variant="secondary">Pending</Badge>
                </div>
                <div className="flex items-center justify-between p-3 border rounded">
                  <div>
                    <span className="font-medium">New Product: iPad Pro M4</span>
                    <p className="text-sm text-gray-600">Add to Electronics category</p>
                  </div>
                  <Badge variant="default">Approved</Badge>
                </div>
                <div className="flex items-center justify-between p-3 border rounded">
                  <div>
                    <span className="font-medium">Price Update: Samsung Galaxy S24</span>
                    <p className="text-sm text-gray-600">Update to ₹85,999</p>
                  </div>
                  <Badge variant="destructive">Rejected</Badge>
                </div>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      {/* Sync Status Footer */}
      <Card>
        <CardContent className="p-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-2">
              <div className={`w-3 h-3 rounded-full ${
                syncStatus === "Connected" ? "bg-green-500" : 
                syncStatus === "Syncing..." ? "bg-yellow-500" : "bg-red-500"
              }`}></div>
              <span className="text-sm font-medium">
                {syncStatus === "Connected" ? "Connected to Admin System" :
                 syncStatus === "Syncing..." ? "Synchronizing with Admin..." :
                 "Sync Complete"}
              </span>
            </div>
            <div className="text-xs text-gray-600">
              Last sync: {new Date().toLocaleString()}
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
